.cases {
  margin-bottom: 68px;
  padding: 83px 0 116px;

  background-color: $color-sapphire-4;

  @include vp-1279 {
    padding: 60px 0 65px;
  }

  @include vp-1023 {
    margin-bottom: 63px;
    padding: 43px 0 57px;
  }

  @include vp-767 {
    padding: 21px 0 57px;
  }

  &__title {
    margin-bottom: 31px;

    font-weight: 800;
    font-size: 46px;
    line-height: 69px;

    @include vp-1023 {
      margin-bottom: 23px;

      font-size: 36px;
      line-height: 54px;
    }

    @include vp-767 {
      margin-bottom: 12px;

      font-size: 22px;
      line-height: 33px;
      text-align: left;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    gap: 0 20px;

    list-style: none;

    @include vp-767 {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__item {
    position: relative;

    min-height: 421px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    border-radius: 30px;

    @include vp-1279 {
      min-height: 314px;
    }

    @include vp-1023 {
      min-height: 243px;
    }

    @include vp-767 {
      min-height: 274px;
    }
  }

  &__item--2,
  &__item--3 {
    @include vp-767 {
      display: none;
    }
  }

  &__img {
    position: relative;

    width: 100%;
    min-height: 250px;
    margin-bottom: 31px;

    border-radius: 30px;

    @include vp-1279 {
      min-height: 178px;
      margin-bottom: 25px;
    }

    @include vp-1023 {
      min-height: 140px;
      margin-bottom: 13px;
    }

    @include vp-767 {
      min-height: 194px;
      margin-bottom: 10px;
    }
  }

  &__img-bg {
    width: 100%;
    height: 100%;

    object-fit: cover;
    border-radius: 30px;
  }

