  &__info {
    padding: 0 20px 20px;

    @include vp-1023 {
      padding: 0 10px 10px;
    }

    @include vp-767 {
      padding: 0;
    }
  }

  &__info-title {
    margin-bottom: 10px;

    font-weight: 800;
    font-size: 22px;
    line-height: 26px;

    text-align: left;

    @include vp-1279 {
      font-size: 20px;
    }

    @include vp-1023 {
      margin-bottom: 4px;

      font-size: 16px;
    }
  }

  &__info-text {
    margin: 0;
    padding: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 26px;

    opacity: 0.6;

    @include vp-1279 {
      font-size: 14px;
      line-height: 25px;
    }

    @include vp-1023 {
      font-size: 12px;
      line-height: 20px;
    }
  }
}
