// используйте для скрытия нежелательных анимаций-скачков при поворотах экрана
@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0;
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}

// Используется для отложенного проявления элемента на странице

@keyframes show {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

// Используется для слайдера

//Desktop
@keyframes slider-top--desktop {
  0% {
    left: 0;
  }

  100% {
    left: -1625px;
  }
}

